import React, { useState, useEffect, useCallback } from 'react';
import NoiseOptions from './NoiseOptions';
import AnalysisOptions from './AnalysisOptions';
import PlotOptions from './PlotOptions';
import NoisePlot from './NoisePlot';
import AnalysisPlot from './AnalysisPlot';
import Papa from 'papaparse'; // Import papaparse library
import "./App.css";


import { generateNoise, performAnalysis } from './utils';

const App = () => {
  const [numPoints, setNumPoints] = useState(256); // Default value for the number of data points
  const [noiseType, setNoiseType] = useState('pink');
  const [analysisType, setAnalysisType] = useState('psd');
  const [plotType, setPlotType] = useState('log-log');
  const [noiseData, setNoiseData] = useState({ x: [], y: [] });
  const [analysisData, setAnalysisData] = useState({ x: [], y: [] });
  const [uploadedData, setUploadedData] = useState(null);


  const handleNumPointsChange = (e) => {
    const value = parseInt(e.target.value, 10);
    if (!isNaN(value) && value > 0) {
      setNumPoints(value);
    }
  };

  const handleNoiseChange = (e) => {
    setNoiseType(e.target.value);
    setUploadedData(null); // Reset uploadedData when noise type changes
  };

  const handleAnalysisChange = (e) => {
    setAnalysisType(e.target.value);
  };


  const handlePlotChange = (e) => {
    setPlotType(e.target.value);
  };

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      Papa.parse(file, {
        header: false,
        dynamicTyping: true,
        complete: (results) => {
          // Check if the data is valid (at least two columns)
          if (results.data.length > 1 && results.data[0].length > 0 && results.data[1].length > 0) {
            const xValues = results.data.map((row) => row[0]);
            const yValues = results.data.map((row) => row[1]);
            setUploadedData({ x: xValues, y: yValues });
          } else {
            console.error('Invalid data format');
          }
        },
        error: (error) => {
          console.error('Error parsing file:', error);
        },
      });
    }
  };

  const downloadCSV = (inputData, filename) => {
    const { data } = inputData;
    // Convert data format to an array of objects with keys 'x' and 'y'
    const formattedData = data.x.map((value, index) => ({ x: value, y: data.y[index] }));
    const csvData = Papa.unparse(formattedData);
  
    const blob = new Blob([csvData], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = filename;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };
  
  

  const generate = useCallback(() => {
    try {
      const dataToAnalyze = uploadedData || generateNoise(noiseType, numPoints);
      setNoiseData(dataToAnalyze);
    } catch (error) {
      console.error("Error generating noise:", error);
    }
  }, [noiseType, uploadedData, numPoints]);

  const analyze = useCallback(() => {
    try {
      const analysisResult = performAnalysis(noiseData, analysisType);
      setAnalysisData(analysisResult);
    } catch (error) {
      console.error("Error analyzing noise:", error);
    }
  }, [analysisType, noiseData]);
  

  useEffect(() => {
    generate();
  }, [noiseType, uploadedData, generate]);

  useEffect(() => {
    analyze();
  }, [noiseData, analysisType, analyze]);

  const fontSize = '16px';

  return (
    <div className="app-container">
      <div className="user-inputs">
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
          <label style={{ marginRight: '10px', fontSize: fontSize}}>Noise Type:</label>
        </div>
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
          <NoiseOptions onChange={handleNoiseChange} fontSize={fontSize} /> 
        </div>
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
          <label style={{ marginRight: '10px', fontSize: fontSize }}>Analysis Type:</label>
        </div>
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
          <AnalysisOptions onChange={handleAnalysisChange} fontSize={fontSize} /> 
        </div>
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
          <label style={{ marginRight: '10px', fontSize: fontSize }}>Plot Type:</label>
        </div>
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
          <PlotOptions onChange={handlePlotChange}fontSize={fontSize} /> 
        </div>
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px' }}>
          <label style={{ marginRight: '10px', fontSize: fontSize}}>Number of Points:</label>
          <input type="number" value={numPoints} onChange={handleNumPointsChange} style={{ width: '100px', fontSize: fontSize}} /> 
        </div>
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: '10px'}}>
          <label style={{ marginRight: '10px', fontSize: fontSize }}>Custom Noise (.csv):</label>
          <input type="file" style={{ fontSize: fontSize }} accept=".csv" onChange={handleFileUpload} /> 
        </div>
        <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '20px' }}>
          <button onClick={() => downloadCSV({ data: analysisData, fields: ['x', 'y'] }, 'analysisData.csv')} style={{ backgroundColor: '#3fb5ff', color: 'white', padding: '8px 16px', fontSize: fontSize,border: 'none', borderRadius: '4px', cursor: 'pointer' }}>
            Download Analysis Data
          </button>
        </div>
      </div>
      <div className="plots">
        <div className="noise-plot-container">
          <NoisePlot data={noiseData} noiseType={noiseType} />
        </div>
        <div className="analysis-plot-container">
          <AnalysisPlot data={analysisData} noiseType={noiseType} analysisType={analysisType} plotType={plotType} />
        </div>
      </div>
    </div>
  );
};
export default App;


