import React from 'react';

const AnalysisOptions = ({ onChange, fontSize }) => {
  const analysisTypes = [
    { label: 'Power Spectral Density', value: 'psd' },
    { label: 'Fourier Transform', value: 'frequency-spectrum' },
    { label: 'Detrended Fluctuation Analysis', value: 'dfa' },
    { label: 'Multifractal DFA', value: 'mfdfa' },
    { label: 'Cumulative Sum', value: 'cumulative-sum' },
    { label: 'Cumulative Sum After Zero-centering', value: 'cumulative-sum-zero-center' },
  ];

  return (
    <div>
      {analysisTypes.map((type) => (
        <div key={type.value} style={{ display: 'flex', alignItems: 'center' }}>
          <input
            type="radio"
            id={type.value}
            name="analysisType"
            value={type.value}
            onChange={onChange}
            style={{ marginRight: '5px' }}
          />
          <label htmlFor={type.value} style={{ fontSize: fontSize }}>
            {type.label}
          </label>
        </div>
      ))}
    </div>
  );
};

export default AnalysisOptions;

