import React from 'react';
import Plot from 'react-plotly.js';

const NoisePlot = ({ data, noiseType }) => {
  var noiseTitle = "";
  if (noiseType === 'pink') {
    noiseTitle = "Pink Noise";
  } else if (noiseType === 'brown') {
    noiseTitle = "Brown Noise/Random Walk";
  } else if (noiseType === 'gaussian-white') {
    noiseTitle = "Gaussian White Noise";
  } else {
    noiseTitle = "Invalid Noise Type";
  }

  var layout = {
    width: 400,
    height: 300,
    margin: {
      l: 50,
      r: 50,
      t: 50,
      b: 50,
    },
    title: {
      text: noiseTitle,
      font: {
        family: 'Arial, sans-serif',
        size: 20,
        color: '#2a3f5f',
      },
    },
    xaxis: {
      title: {
        font: {
          family: 'Arial, sans-serif',
          size: 18,
          color: '#2a3f5f',
        },
      },
      showgrid: false, // Remove grid lines
    },
    yaxis: {
      title: {
        font: {
          family: 'Arial, sans-serif',
          size: 18,
          color: '#2a3f5f',
        },
      },
      showgrid: false, // Remove grid lines
    },
  };

  return (
    <Plot
      data={[
        {
          x: data.x,
          y: data.y,
          type: 'scatter',
          mode: 'lines',
          name: 'Output',
          line: { color: '3fb5ff', width: 3 },
        },
      ]}
      layout={layout}
    />
  );
};

export default NoisePlot;

