import React from 'react';

const NoiseOptions = ({ onChange, fontSize }) => {
  const noiseTypes = [
    { label: 'Pink Noise', value: 'pink' },
    { label: 'Brown Noise/Random Walk', value: 'brown' },
    { label: 'Gaussian White Noise', value: 'gaussian-white' },
  ];

  return (
    <div>
      {noiseTypes.map((type) => (
        <div key={type.value} style={{ display: 'flex', alignItems: 'center' }}>
          <input
            type="radio"
            id={type.value}
            name="noiseType"
            value={type.value}
            onChange={onChange}
            style={{ marginRight: '5px' }}
          />
          <label htmlFor={type.value} style={{ fontSize: fontSize }}>
            {type.label}
          </label>
        </div>
      ))}
    </div>
  );
};

export default NoiseOptions;

