import React from 'react';

const PlotOptions = ({ onChange, fontSize }) => {
  const plotTypes = [
    { label: 'Log-Log', value: 'log-log' },
    { label: 'Regular', value: 'regular' },
  ];

  return (
    <div>
      {plotTypes.map((type) => (
        <div key={type.value} style={{ display: 'flex', alignItems: 'center' }}>
          <input
            type="radio"
            id={type.value}
            name="plotType"
            value={type.value}
            onChange={onChange}
            style={{ marginRight: '5px' }}
          />
          <label htmlFor={type.value} style={{ fontSize: fontSize }}>
            {type.label}
          </label>
        </div>
      ))}
    </div>
  );
};

export default PlotOptions;
