import React from 'react';
import Plot from 'react-plotly.js';
import { bestFitPowerLaw, oneOverXData, oneOverX2Data } from './utils';


const AnalysisPlot = ({ data, noiseType, analysisType, plotType }) => {

  function drawBestFitPowerLaw(data) {
    const bestFitPower = bestFitPowerLaw(data);
    return {x: data.x, y: bestFitPower.bestFitY, type: 'scatter', mode: 'lines', name: `Slope: ${bestFitPower.slope}`, line: { dash: 'dot', color: 'red' } };
  }

  function drawOneOverXData(data) {
    return {x: data.x, y: oneOverXData(data), type: 'scatter', mode: 'lines', name: 'y = 1/f', line: { dash: 'dot', color: 'red' } };
  }

  function drawOneOverX2Data(data) {
    return {x: data.x, y: oneOverX2Data(data), type: 'scatter', mode: 'lines', name: 'y = 1/f^2', line: { dash: 'dot', color: 'red' } };
  }


  //In case you want to fit a curve to a plot
  var curveDrawn = {};
  switch (noiseType) {
    case 'pink':
      if (analysisType === 'psd') {
        //curveDrawn = {};
        if (plotType === 'log-log') {
          curveDrawn = drawBestFitPowerLaw(data);
        } else {
          curveDrawn = drawOneOverXData(data);
        }
      } else if (analysisType === 'dfa' && plotType === 'log-log') {
        curveDrawn = drawBestFitPowerLaw(data);
      }
      break;
    case 'brown':
      if (analysisType === 'psd') {
        if (plotType === 'log-log') {
          curveDrawn = drawBestFitPowerLaw(data);
        } else {
          curveDrawn = drawOneOverX2Data(data);
        }
      } else if (analysisType === 'dfa' && plotType === 'log-log') {
        curveDrawn = drawBestFitPowerLaw(data);
      }
      break;
    case 'gaussian-white':
      if (analysisType === 'dfa' && plotType === 'log-log') {
        curveDrawn = drawBestFitPowerLaw(data);
      }
      break;
    default:
      throw new Error('Invalid noise type');
  }

  var analysisTitle = "";
  if (analysisType === 'psd') {
    analysisTitle = "Power Spectral Density";
  } else if (analysisType === 'frequency-spectrum') {
    analysisTitle = "Frequency Spectrum (Fourier Transform)";
  } else if (analysisType === 'dfa') {
    analysisTitle = "Detrended Fluctuation Analysis";
  } else if (analysisType === 'mfdfa') {
    analysisTitle = "Multifractal Detrended Fluctuation Analysis";
  } else if (analysisType === 'cumulative-sum') {
    analysisTitle = "Cumulative Sum";
  } else if (analysisType === 'cumulative-sum-zero-center') {
    analysisTitle = "Cumulative Sum (of Zero-centered Signal)";
  } else {
    analysisTitle = "Invalid Analysis Type";
  }

  var layout = {
    width: 400,
    height: 300,
    margin: {
      l: 50,
      r: 50,
      t: 50,
      b: 50,
    },
    title: {
      text: analysisTitle,
      font: {
        family: 'Arial, sans-serif',
        size: 20,
        color: '#2a3f5f',
      },
    },
    xaxis: {
      title: {
        font: {
          family: 'Arial, sans-serif',
          size: 18,
          color: '#2a3f5f',
        },
      },
      type: plotType === 'log-log' ? 'log' : 'linear',
      showgrid: false, // Remove grid lines
    },
    yaxis: {
      title: {
        font: {
          family: 'Arial, sans-serif',
          size: 18,
          color: '#2a3f5f',
        },
      },
      type: plotType === 'log-log' ? 'log' : 'linear',
      showgrid: false, // Remove grid lines
    },
  };

  return (
    <Plot
      data={[
        // Original data plot
        {
          x: data.x,
          y: data.y,
          type: 'scatter',
          mode: 'lines',
          name: 'Output',
          line: { color: '3fb5ff', width: 3 },
        },
        curveDrawn,
      ]}
      layout={layout}
    />
  );
};

export default AnalysisPlot;